import { GetComponentProps, styled } from 'topwrite';
import { useEffect, useMemo, useRef } from 'react';
import parseContent from '@/utils/parse-content';
import videojs from 'video.js';
import 'video.js/dist/video-js.min.css';

export default function Video({ props: { value } }: GetComponentProps<'x-block'>) {

    const video = useMemo(() => {
        return parseContent(value);
    }, [value]);

    const container = useRef(null);

    const key = useMemo(() => new Date().toString(), [video]);

    useEffect(() => {
        if (container.current) {
            const player = videojs(container.current, {
                controls: true,
                fluid: true,
                preload: 'auto',
                sources: [{
                    src: video.src
                }],
                ...video
            });

            return () => {
                player.dispose();
            };
        }
    }, [video]);

    return <Container key={key}>
        <div data-vjs-player>
            <video ref={container} className={'video-js'} />
        </div>
    </Container>;
}

const Container = styled.div`
  &:not( :last-child) {
    margin-bottom: 15px;
  }
`;
